body {
  background: #f4f7f6;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #1d2120;
  font-family: 'Oswald', sans-serif;
}
.btn {
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}
.btn-primary {
  color: #f4f7f6;
  background-color: #1d2120;
  border-color: #000000;
}
.btn-primary:hover {
  color: #1d2120;
  background-color: #49c5b6;
  border-color: #35a698;
}
.btn-primary:active {
  background-color: #1d2120;
  border-color: #000000;
}
.btn-primary:focus {
  background-color: #bcd5d1;
  border-color: #7daea6;
  color: #1d2120;
}
.btn-info {
  color: #f4f7f6;
  background-color: #942d20;
  border-color: #40140e;
}
.btn-info:hover {
  color: #1d2120;
  background-color: #49c5b6;
  border-color: #35a698;
}
.btn-info:active {
  background-color: #49c5b6;
  border-color: #35a698;
}
.btn-info:focus {
  background-color: #bcd5d1;
  border-color: #7daea6;
  color: #1d2120;
}
.form-group > label {
  width: 100%;
}
.form-group .form-control {
  margin-top: .5rem;
}
.page-title {
  margin-bottom: 3rem;
}
@media only screen and (max-width:  767px ) {
  .page-title {
    margin-top: 2rem;
  }
}
.site-wrapper {
  display: grid;
  grid-gap: 1.5rem;
  grid-template-areas: "header" "body" "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 6rem auto auto;
}
.site-wrapper header {
  grid-area: header;
}
.site-wrapper > main {
  grid-area: body;
  margin: 6rem auto 3rem;
  width: 100%;
  max-width: 120rem;
  padding: 0 3rem;
  min-height: 80vh;
}
@media only screen and (max-width:  767px ) {
  .site-wrapper > main {
    margin-top: 3rem;
    padding: 0 1.5rem;
  }
}
.site-wrapper footer {
  grid-area: footer;
}
.left-column-wrap {
  display: grid;
  grid-column-gap: 3rem;
  grid-template-areas: "left-column right-column";
  grid-template-columns: 25% 1fr;
}
@media only screen and (max-width:  767px ) {
  .left-column-wrap {
    grid-template-areas: "left-column" "right-column";
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}
.left-column-wrap .left-column {
  grid-area: left-column;
}
.left-column-wrap .right-column {
  grid-area: right-column;
}
.one-col-center {
  width: 50%;
  margin: 0 auto;
}
@media only screen and (min-width: 768px) and (max-width:  1023px ) {
  .one-col-center {
    width: 75%;
  }
}
@media only screen and (max-width:  767px ) {
  .one-col-center {
    width: 100%;
  }
}



