.login-links {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
}
.login-links ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}
.login-links ul li {
  display: -ms-flexbox;
  display: flex;
  padding: 0 3rem;
  background: #49c5b6;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.login-links ul li a {
  color: #1d2120;
  text-transform: uppercase;
  font-family: 'Oswald', sans-serif;
  text-decoration: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  letter-spacing: .1rem;
}
.login-links ul li a:before {
  content: '\f007';
  font-family: 'FontAwesome';
  margin-right: 1rem;
  background: #1d2120;
  color: #49c5b6;
  border-radius: 50%;
  padding: .5rem .75rem;
}
.login-links ul li:hover {
  background: #bcd5d1;
}
.login-links ul li:hover a {
  color: #1d2120;
  text-decoration: none;
}
.login-links ul li:hover a:before {
  background: #1d2120;
  color: #bcd5d1;
}
.login-links ul li:hover button {
  color: #49c5b6;
  text-decoration: none;
}
.login-links ul li:hover button:before {
  background: #f4f7f6;
  color: #49c5b6;
}
.login-links ul li button {
  outline: 0;
  border: none;
  color: #f4f7f6;
  text-decoration: none;
  background: transparent;
}
.login-links ul li button:before {
  content: '\f007';
  font-family: 'FontAwesome';
  margin-right: 1rem;
  background: #1d2120;
  color: #f4f7f6;
  border-radius: 50%;
  padding: .5rem .75rem;
}



