@-webkit-keyframes blurFadeIn {
  0% {
    opacity: 0;
    text-shadow: 0px 0px 40px #1d2120;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  50% {
    opacity: 1;
    text-shadow: 0px 0px 10px #1d2120;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    opacity: 1;
    text-shadow: 0px #1d2120;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes blurFadeIn {
