.home {
  float: left;
  width: 100%;
  height: 100vh;
}
.home video {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  background-size: cover;
  -webkit-transition: 1s opacity;
  -o-transition: 1s opacity;
  transition: 1s opacity;
  opacity: .25;
}
.home h2 {
  -webkit-animation: blurFadeInOut 3s ease-in backwards;
          animation: blurFadeInOut 3s ease-in backwards;
  color: transparent;
  position: absolute;
  top: 33%;
  right: 0;
  left: 0;
  text-align: center;
  line-height: 1.5;
}
.home .content {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
  z-index: -10;
}
.home .content .frame-1 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
.home .content .frame-2 {
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
.home .content .frame-3 {
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}
.home .content .frame-4 {
  -webkit-animation-delay: 9s;
          animation-delay: 9s;
}
.home .content .frame-5 {
  -webkit-animation-delay: 12s;
          animation-delay: 12s;
  -webkit-animation: blurFadeInNoShadow 3s ease-in 12s backwards;
          animation: blurFadeInNoShadow 3s ease-in 12s backwards;
  color: #1d2120;
}



