.btn {
  position: relative;
}
.btn.btn-social {
  position: relative;
  padding-left: 4.4rem;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.btn.btn-social span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 3.2rem;
  line-height: 3.4rem;
  font-size: 2.2rem;
  text-align: center;
  border-right: 1px solid #5a5c51;
}
.btn.btn-social:hover span {
  border-right-color: #35a698;
}



