.project .image-wrap {
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));
  margin-bottom: 2rem;
}
.project .image-wrap .modal-button {
  border: none;
  outline: 0;
  padding: 0;
}
.project .image-wrap img {
  width: 100%;
  cursor: pointer;
}
.project .content-wrap {
  float: left;
  width: 100%;
}
.project .content-wrap h1 {
  margin-top: 0;
}
.project .content-wrap .title {
  font-size: 2.4rem;
  color: #22264b;
}



