.backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.3);
}
.backdrop .pop-modal {
  background: #fff;
  max-width: 50rem;
  max-height: 75vh;
  margin: 6rem auto 0;
  position: relative;
}
@media only screen and (min-width: 1024px) and (max-width:  1179px ) {
  .backdrop .pop-modal {
    max-width: 35rem;
  }
}
@media only screen and (min-width: 768px) and (max-width:  1023px ) {
  .backdrop .pop-modal {
    max-width: 35rem;
  }
}
@media only screen and (max-width:  767px ) {
  .backdrop .pop-modal {
    max-width: 28rem;
  }
}
.backdrop .pop-modal .btn {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 0;
}



