.work {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-bottom: 6rem;
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(24rem, 1fr));
}
.work .work-item .card {
  float: left;
  width: 100%;
  overflow: hidden;
  text-align: center;
  position: relative;
  margin-bottom: 3rem;
}
.work .work-item .card img {
  max-width: 100%;
  -webkit-transform: scaleY(1);
      -ms-transform: scaleY(1);
          transform: scaleY(1);
  -webkit-transition: all 0.7s ease-in-out;
  -o-transition: all 0.7s ease-in-out;
  transition: all 0.7s ease-in-out;
  position: relative;
  opacity: 1;
}
.work .work-item .card:hover img {
  opacity: 0;
  -webkit-transform: scale(10);
      -ms-transform: scale(10);
          transform: scale(10);
}
.work .work-item .card:hover .mask {
  opacity: 1;
  background: rgba(255, 255, 255, 0.9);
}
.work .work-item .card:hover .mask h3 {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}
.work .work-item .card:hover .mask button {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}
.work .work-item .mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  -webkit-transition: all .5s linear;
  -o-transition: all .5s linear;
  transition: all .5s linear;
  opacity: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}
.work .work-item .mask h3 {
  -webkit-transition: all .5s linear;
  -o-transition: all .5s linear;
  transition: all .5s linear;
  opacity: 0;
  color: #1d2120;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
}
.work .work-item .mask button {
  -webkit-transition: all .5s linear;
  -o-transition: all .5s linear;
  transition: all .5s linear;
  opacity: 0;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
}



