.about .portrait-wrap {
  float: left;
  width: 100%;
}
@media only screen and (max-width:  767px ) {
  .about .portrait-wrap {
    text-align: center;
  }
}
.about .portrait-wrap img {
  max-width: 100%;
  width: 100%;
  border-radius: 50%;
}
@media only screen and (max-width:  767px ) {
  .about .portrait-wrap img {
    max-width: 30rem !important;
  }
}
.about .portrait-wrap ul {
  float: left;
  width: 100%;
  padding: 1rem 0;
  margin: 0;
  list-style: none;
}
@media only screen and (max-width:  767px ) {
  .about .portrait-wrap ul {
    max-width: 30rem;
    float: none;
    margin: 0 auto;
  }
}
.about .portrait-wrap ul li {
  float: left;
  width: 100%;
  font-family: 'Oswald', sans-serif;
  font-size: 1.5rem;
  padding: .5rem 0;
  border-bottom: 1px solid #1d2120;
}
.about .portrait-wrap ul li:last-child {
  border-bottom: none;
}
.about .copy-wrap {
  float: left;
  width: 100%;
  padding-bottom: 1.5rem;
}
.about .chart-wrap {
  float: left;
  padding: 0 1.5rem;
  width: 33.333%;
}
@media only screen and (max-width:  767px ) {
  .about .chart-wrap {
    width: 50%;
  }
}
.about .chart-wrap .chart {
  position: relative;
  color: #5a5c51;
  width: 8rem;
}
.about .chart-wrap .chart.html svg .outer {
  stroke-dashoffset: 133;
  -webkit-animation: show 2s;
          animation: show 2s;
  stroke-dasharray: 330;
}
.about .chart-wrap .chart.less svg .outer {
  stroke-dashoffset: 133;
  -webkit-animation: show 2s;
          animation: show 2s;
  stroke-dasharray: 310;
}
.about .chart-wrap .chart.jquery svg .outer {
  stroke-dashoffset: 133;
  -webkit-animation: show 2s;
          animation: show 2s;
  stroke-dasharray: 310;
}
.about .chart-wrap .chart.js .icon {
  font-size: 4rem;
  top: 3rem;
}
.about .chart-wrap .chart.js svg .outer {
  stroke-dashoffset: 133;
  -webkit-animation: show 2s;
          animation: show 2s;
  stroke-dasharray: 290;
}
.about .chart-wrap .chart.react svg .outer {
  stroke-dashoffset: 133;
  -webkit-animation: show 2s;
          animation: show 2s;
  stroke-dasharray: 290;
}
.about .chart-wrap .chart.grunt svg .outer {
  stroke-dashoffset: 133;
  -webkit-animation: show 2s;
          animation: show 2s;
  stroke-dasharray: 310;
}
.about .chart-wrap .chart figcaption {
  padding: 4rem 1rem;
  border-radius: 100px;
  text-indent: -99999rem;
}
.about .chart-wrap .chart .icon {
  position: absolute;
  font-size: 6rem;
  top: 2.25rem;
  width: 100px;
  text-align: center;
  color: #1d2120;
}
.about .chart-wrap .chart svg {
  position: absolute;
  top: 0;
}
.about .chart-wrap .chart svg .outer {
  stroke: #1d2120;
  fill: transparent;
  stroke: #49c5b6;
  stroke-width: 5;
  stroke-dasharray: 330;
  -webkit-transition: stroke-dashoffset 1s;
  -o-transition: stroke-dashoffset 1s;
  transition: stroke-dashoffset 1s;
  -webkit-animation-play-state: running;
  -moz-transform: rotate(-89deg) translateX(-190px);
}
.about .chart-wrap .chart svg .outer:hover {
  stroke-dashoffset: 330 !important;
  -webkit-animation-play-state: paused;
}



